import React from 'react';
import "./Body.scss";

export default function Body() {
  return (
    <div id="bodyDiv">
      <div className="container topDiv">
        <div id="mainHeader">
            <h1>EU Migration to and from UK</h1>
            <h2>20 Nov 2024</h2>
        </div>
        <div className="Subcontainer Div">
          <div id="subheader">
            <h3>How many EU migrants are there in the UK? How has the migration of EU citizens changed since Brexit? This briefing provides key statistics on EU migrants and migration in the UK.</h3>
          </div>
        </div>

        
        <div className="expander">
            <div className="Open init">
                  <div id="a0-0">
                    <span className="on">
                      Key Points:
                    </span>
                    </div>
                    </div>
            <ul>
                <li>
                    Net migration of EU citizens has been negative since the pandemic and under the post-Brexit immigration system, with immigration falling by almost 70% compared to its 2016 peak.
                    <a className="read-more" href="#hiv-more">Read More...</a>
                </li>
                <li>
                    In 2021, there were approximately 4 million EU-born residents in the UK, making up 6% of the population and 37% of all those born abroad. 
                    <a className="read-more" href="#hcv-more">Read More...</a>
                </li>
                <li>
                    The top origin country for EU-born residents is Poland (21%), followed by Romania (14%), the Republic of Ireland (10%), Germany (7%), and Italy (7%). 
                    <a className="read-more" href="#anf-more">Read More...</a>
                </li>
                <li>
                    EU Migrants in the UK are particularly concentrated in London, although to a lesser extent than those from outside the EU. 
                    <a className="read-more" href="#ahr-more">Read More...</a>
                </li>
            </ul>
        </div>
        <div className="expander-content-div">
            <div id="hiv-more" className="expander-content">
                <p>
                  Net migration of EU citizens has been negative since the pandemic and under the post-Brexit immigration system, with immigration falling by almost 70% compared to its 2016 peak.</p>
                <p class="sub-paragraph">
                In the year ending December 2022, the long-term net migration of EU citizens to the UK was negative, at -51,000, according to Office for National Statistics (ONS) estimates published in May 2023. Net migration from the EU turned negative in 2020, after the beginning of the pandemic, and continued falling in 2021 and 2022 (Figure 1). This marks a significant change compared to the previous two decades of positive EU net migration. In this time, there were two major peaks in migration from the EU. The first occurred shortly after the 2004 enlargement of the EU and ended following the financial crisis in 2008. The second came in 2013-15, mostly driven by an increase in immigration from the EU-2 countries (Romania and Bulgaria) after restrictions on their right to work in Britain were lifted. In the year ending March 2016, EU net migration peaked at over 280,000, with more than 500,000 EU nationals moving to the UK.
                </p>
</div>
            </div>
            <div id="hiv-dashboard" className="expander-content">
                <iframe src="https://public.tableau.com/views/EU2023/FIG1?:showVizHome=no&:embed=true"
                    height="827px"></iframe>
                <hr />      
            </div>
            <div id="hcv-more" className="expander-content">
                <p>The HCV dashboard provides an in-depth analysis of market share and duration of treatment,
                    highlighting
                    trends and regional variations that are crucial for understanding the landscape.</p>
                    <p class="sub-paragraph_1">
                      The decline in EU net migration particularly affected EU-8 nationals: those from Eastern European countries that joined the EU in 2004, such as Poland. Between the 2016 and 2020 fiscal years, net migration of EU-8 citizens fell by 126%, from 59,000 to -15,000. By contrast, net migration from both EU-14 and EU-2 countries fell by around 40% in the same period (Figure 2). It is unclear if this trend continued after 2020, when the sharpest drop in overall EU net migration was observed, as the pandemic disrupted the collection of migration statistics (for more detail, see Evidence Gaps and Limitations).
                      </p>
            </div>
            <div id="hcv-dashboard" className="expander-content">
                <iframe src="https://public.tableau.com/views/EU2023/FIG1?:showVizHome=no&:embed=true"
                    height="727px"></iframe>
                <hr />
            </div>
            <div id="anf-more" className="expander-content">
                <p>In 2021, there were approximately 4 million EU-born residents in the UK, making up 6% of the population and 37% of all those born abroad
                </p>
                <p class="sub-paragraph_2">
                      According to our 2021 population estimates, about a fifth of EU-born residents in the UK are Polish (21%, 826,000). The next largest groups are people born in Romania (14%, 554,000), Ireland (10%, 389,000), Germany (7%, 298,000), and Italy (7%, 298,000). Compared to the previous census in 2011, by far the largest increase in population was seen among those born in the EU-2 countries, Romania and Bulgaria. The Romanian-born population increased almost 7-fold, from a little over 82,000 in 2011. Over the same period, the Bulgarian-born population grew from under 50,000 to around 159,000. The number of UK residents born in Southern European states like Italy, Spain, and Greece also more than doubled in the ten years to 2021. There was substantial variation among top origin countries (Table 1), with smaller increases in the number of people born in Poland and France, a relatively constant number of German-born residents, and a notable fall in the number of those born in the Republic of Ireland.
                      </p>

            </div>
            <div id="anf-dashboard" className="expander-content">
                <iframe src="https://public.tableau.com/views/EU2023/FIG1?:showVizHome=no&:embed=true"
                    height="727px"></iframe>
                <hr />
            </div>
            <div id="ahr-more" className="expander-content">
                <p>
                  EU migrants in the UK are particularly concentrated in London, although to a lesser extent than those from outside the EU
                </p>
                <p class="sub-paragraph_3">
                      Before 2019, the number of EU employments grew steadily – by 45% between July 2014 and November 2019, compared to a much smaller rise among non-EU migrants. Much of the increase was driven by EU-2 nationals from Romania and Bulgaria, whose number more than tripled over the same period of time. The employment of EU nationals, however, peaked in November 2019 at around 2.66 million, and declined sharply during the pandemic, falling by more than 7% as of December 2022. Once again, the sharpest decline in numbers was found among EU-8 nationals. For this group, employments in the UK plateaued around 2016-17 and fell by more than 14% between 2019 and 2022.
                      </p>
            <div id="ahr-dashboard" className="expander-content">
                <iframe src="https://public.tableau.com/views/EU2023/FIG1?:showVizHome=no&:embed=true"
                    height="727px"></iframe>
                <hr />
            </div>
        </div>

</div>
    </div>
  )
}